.form__checkbox {
  margin-bottom: 20px;
}

.form__checkbox input[type="checkbox"] {
  display: none;
}

.form__checkbox input[type="checkbox"]:disabled {
  opacity: 0.3;
}

.form__checkbox input[type="checkbox"] + label::after {
  content: none;
}

.form__checkbox input[type="checkbox"]:checked + label::after {
  content: "";
}

.form__checkbox label {
  display: inline-block;
  position: relative;

  padding-left: 35px;
}

.form__checkbox label::before,
.form__checkbox label::after {
  position: absolute;
  display: inline-block;
  content: "";
}

.form__checkbox label::before {
  position: absolute;
  top: -2px;
  left: 0px;
  width: 25px;
  height: 25px;
  background: #f7f7f7;
  border-radius: 3px;
  border: 1px solid rgba(52, 192, 111, 0.1);
}

.form__checkbox label::after {
  position: absolute;
  top: 5px;
  left: 7px;
  width: 12px;
  height: 6px;
  border-bottom: 2px solid #52b68d;
  border-left: 2px solid #52b68d;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

