/* ----------------------------------------------- 
 Green Color - #52B68D  
 Red Color - #ff5054 
 ----------------------------------------------- */

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

html,
body {
  background-color: #fff;
  color: #555;
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 300;
  font-size: 20px;
  text-rendering: optimizeLegibility;
}

section {
  -webkit-appearance: none;
  flex: 1 1;
  padding: 60px 0px;
  background-color: #f2f2f2;
}

input {
  display: block;
  width: 100%;
  color: #555;
  border: 0;
  font-weight: 300;
  background: none;
  cursor: pointer;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-transition: "color 9999s ease-out, background-color 9999s ease-out";
  -webkit-transition-delay: 9999s;
}

h1,
h2,
h3 {
  font-weight: 300;
  text-transform: uppercase;
}

h1 {
  margin-top: 0px;
  margin-bottom: 20px;
  font-size: 240%;
  color: #fff;
  word-spacing: 4px;
  letter-spacing: 1px;
}

h2 {
  font-size: 180%;
  word-spacing: 2px;
  letter-spacing: 1px;
  text-align: center;
  margin-bottom: 20px;
}

h2:after {
  display: block;
  content: " ";
  width: 100px;
  height: 2px;
  background-color: #52b68d;
  margin: 0 auto;
  margin-top: 30px;
}

h3 {
  font-size: 110%;
  margin-bottom: 30px;
}

a {
  text-decoration: none;
  color: #fff;
}

ul {
  list-style: none;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.row {
  max-width: 1140px;
  margin: 0 auto;
}

.btn:link,
.btn:visited {
  flex: 0 0 30%;
  display: flex;
  justify-content: center;
  border: 1px solid #52b68d;
  padding: 15px 0px;
  border-radius: 5px;
  -webkit-transition: border 0.2s;
  transition: border 0.2s;
}

.btn-full:link,
.btn-full:visited {
  background-color: #52b68d;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
}

.btn-ghost:link,
.btn-ghost:visited {
  color: #52b68d;
  background-color: transparent;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
}

.btn-full:hover,
.btn-full:active {
  border: 1px solid #3a8a69;
  background-color: #3a8a69;
}

.btn-ghost:hover,
.btn-ghost:active {
  color: #fff;
  border: 1px solid #3a8a69;
  background-color: #3a8a69;
}

.btn-action:link,
.btn-action:visited {
  background-color: #ff7275;
  border-color: #ff7275;
  border-radius: 30px;
  font-weight: 400;
  padding: 20px 0px;
  text-transform: uppercase;

  -webkit-transition: background-color 0.2s, border-color 0.2s;

  transition: background-color 0.2s, border-color 0.2s;
}

.btn-action:hover,
.btn-action:active {
  background-color: #ff5054;
  border-color: #ff5054;
}

.col {
  margin: 2% 1%;
}

.box {
  padding: 0 2%;
}

.icon-big {
  display: block;
  font-size: 350%;
  color: #52b68d;
  margin-bottom: 10px;
}

